<template lang="html">
  <div class="login">
    <div class="">
      <h5>诚信小铺上货端--登录</h5>
    </div>
    <el-form :model="ruleForm2" status-icon  ref="ruleForm2" label-width="100px" class="demo-ruleForm">
  <el-form-item label="账号：" prop="name">
    <el-input type="text" v-model="ruleForm2.name" auto-complete="off"></el-input>
  </el-form-item>
  <el-form-item label="密码：" prop="pass">
    <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
  </el-form-item>

  <el-form-item class="operateBox">
    <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
    <el-button @click="resetForm('ruleForm2')">重置</el-button>
  </el-form-item>
</el-form>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name:'login',
    data() {
      return {
        ruleForm2: {
          name: '',
          pass: ''
        },
      };
    },
    methods: {
      submitForm(formName) {
        this.axios.post('http://honesty.zhaischool.com/api/login',{name:this.ruleForm2.name,password:this.ruleForm2.pass}).then(
          response => {
            console.log(response);
            if (response.data.data!='false') {
              this.$store.dispatch('login',
              {name:this.ruleForm2.name,supplier_id:response.data.data.supplier_id,supplier_name:response.data.data.supplier_name}).then(
                ()=>{
                  if (this.$store.state.login) {
                    this.$store.dispatch('show').then(()=>{
                      this.open(true);
                      this.$router.push({path:'/'});
                    })
                  }
                }
              );
            }
            else{
              this.open(false);
            }
          }
        )
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      open(status) {
        if(status){
          this.$message({
            message:'登录成功',
            type:"success"
          });
        }
        else {
          this.$message({
            message:'登录失败，检查账号和密码',
            type:"warning"
          });
        }

     },
    }
  }
</script>

<style lang="css">
.login{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.el-form-item__label{
  display: flex;
  justify-content: center;
}
html, body, .app{
  height: 100%;
}
.operateBox >.el-form-item__content{
  margin-left: 0 !important;
}

</style>



