import Vue from 'vue'
import Router from 'vue-router'
import show from '@/components/show'
import operate from '@/components/operate'
import login from '@/components/login'
Vue.use(Router)

export default new Router({
  history: true,
  routes: [
    {
      path: '/',
      name: 'show',
      component: show
    },
    {
      path: '/operate',
      name: 'operate',
      component: operate
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }

  ]
})



