<template lang="html">
  <el-container>
  <el-header>
    <span> {{$store.state.supplier_name}}上货端--上货 </span>
  </el-header>
  <el-main>
    <ul class="list-group">
      <li class="list-group-item" v-for="(goods,index) in $store.state.goods" v-bind:key="goods.pivot.goods_id">
        <el-row>
          <el-col :span="12"><div class="grid-content bg-purple" style="display:flex;flex-direction:column;">
            <span>{{goods.goods_name}}</span> <span class="word">今日上货至{{goods.pivot.shipments}}</span>
          </div></el-col>
          <el-col :span="12"><div class="grid-content bg-purple-light"></div>
            <el-input v-model="goods.added" placeholder="今日上货量"  style="width:80%;"></el-input>
            <el-dropdown size="mini" @command="caveat(index)">
              <span class="el-dropdown-link">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item >缺货</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
      </li>
    </ul>
    <el-row><el-col><el-button type="primary" @click="operate()">提交</el-button></el-col></el-row>
  </el-main>
  <el-footer height="60px">
    <el-row style="height:100%">
      <el-col :span="12" style="height:100%">
        <router-link :to="{ name: 'show'}">
          <div class="grid-content bg-purple" style="display:flex;height:60px" v-on:click="changeView(0)"
          v-bind:class="{'changeRoute':$store.state.view ==0}">
          <span>记录</span>
        </div></router-link>
      </el-col>
      <el-col :span="12" style="height:100%">
        <router-link :to="{ name: 'operate'}"><div class="grid-content bg-purple-light" style="display:flex;height:100%"
          v-on:click="changeView(1)" v-bind:class="{'changeRoute':$store.state.view ==1}">
        <span>操作</span>
      </div></router-link></el-col>

    </el-row>
  </el-footer>
</el-container>
</template>

<script>
/* eslint-disable */
export default {
  name: 'operate',
  created(){
    if (!this.$store.state.login) {
        this.$router.push({path:'/login'})
    }
  },
  components: {

  },

  methods: {
    changeView (value) {
      this.$store.dispatch('changeview', value)
    },
    caveat (index) {
      let content = this.$store.state.supplier_name+"缺少"+this.$store.state.goods[index].goods_name;
      let operator = this.$store.state.operator;
      this.axios.post('http://honesty.zhaischool.com/api/notify',{content:content,operator:operator}).then(response=>{
        console.log(response);
      });
    },
    operate (){
      this.$store.dispatch('operate').then(()=>{
        this.notify();
        if (this.$store.state.record&&this.$store.state.operate) {
          this.confirm();
        }
      });
    },
    notify() {
        const h = this.$createElement;

        this.$notify({
          title: '操作',
          message: h('span', { style: 'color: black'}, '操作保存成功'),
          type:"success"
        });
      },
      confirm() {
        this.$confirm('是否将信息提交至平台', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('submit',this.$store.state.goods);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消提交'
          });
        });
      }
  }
}
</script>

<style lang="css">
  .word{
    -webkit-transform-origin-x: 0;
    -webkit-transform: scale(0.70);
    -moz-osx-transform: scale(0.70);
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .el-header{
    text-align: center;
  }
  .el-container{
    height: 100%;
    display: flex;

  }
  .el-button{
    width: 100%;
  }
  .grid-content{
    justify-content: center;
    align-items: center;
  }
  html,body{
    height: 100%;
  }
</style>



