import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
Vue.use(Vuex)
/* eslint-disable */
export default new Vuex.Store({
  state: {
    goods: [],
    view: 0,
    login:false,
    record:false,
    operate:false,
    supplier_name:'',
    supplier_id:0,
    operator:''
  },
  mutations: {
    supplier (state, data) {
      state.goods = data.goods;
      state.supplier_name = data.supplier_name;
    },
    view (state, value) {
      state.view = value
    },
    saverecord (state) {
      state.record = true;
    },
    saveoperate(state) {
      state.operate = true;
    },
    loginstatus(state,data){

      state.supplier_name = data.supplier_name;
      state.login = true;
      state.supplier_id = data.supplier_id;
      state.operator = data.name;
    }
  },
  actions: {
    show (store) {
      Vue.axios.get('http://honesty.zhaischool.com/api/supplier/goods/'+this.state.supplier_id).then(response => {
        store.commit('supplier', response.data.data[0]);
      })
    },
    changeview (store, value) {
      store.commit('view', value)
    },
    login(store,data){
      store.commit("loginstatus",data);
    },
    record(store){
      store.commit('saverecord');
    },
    operate(store){
      store.commit('saveoperate');
    },
    submit(store,goods){
      axios.post("http://honesty.zhaischool.com/api/supplier/operate/"+this.state.supplier_id,{goods:goods}).then(response => {
        console.log(response);
      })
    }
  }
})



